import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Formik } from 'formik';
import * as Yup from 'yup';
import 'yup-phone';
import { updateProfile } from '../../../store/actions';
import ChangePasswordModal from './ChangePasswordModal';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import {
  FIRSTNAME_REQUIRED,
  LASTNAME_REQUIRED,
  PHONE_VALID,
} from '../../../constants/errorConstants';

// eslint-disable-next-line no-unused-vars
const Profile = props => {
  const [modelOpen, setModelOpen] = useState(false);
  const [isDisable, setIsDisable] = useState(false);
  const [profilePicURL, setProfilePicURL] = useState('');
  const [profilePic, updatePhoto] = useState('');
  const validationSchema = Yup.object().shape({
    firstName: Yup.string()
      .strict(true)
      .trim()
      .label('Firstname')
      .required(FIRSTNAME_REQUIRED),
    lastName: Yup.string()
      .strict(true)
      .trim()
      .label('Lastname')
      .required(LASTNAME_REQUIRED),
    phone: Yup.string().phone('IN', true, PHONE_VALID).required(),
  });
  const {
    firstName: firstNameInitial,
    lastName: lastNameInitial,
    email,
    phone: phoneInitial,
    profilePic: profilePicInitial,
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.auth);
  const { open } = useSelector(state => state.modal);

  useEffect(() => {
    updatePhoto(profilePicInitial);
  }, []);

  const dispatch = useDispatch();

  const updateProfileData = values => {
    setIsDisable(true);
    const requestBody = {
      firstName: values.firstName,
      lastName: values.lastName,
      phone: values.phone,
    };
    if (typeof profilePic === 'object') {
      requestBody.profilePic = profilePic;
    }
    dispatch(updateProfile({ requestBody, setIsDisable }));
  };

  const getImage = evt => {
    const { files } = evt.target;
    if (files[0]) {
      if (files[0].type.includes('image')) {
        const filename = files[0].name;
        const fileExtension = filename.substr(filename.lastIndexOf('.') + 1);
        if (
          fileExtension.toLowerCase() === 'png' ||
          fileExtension.toLowerCase() === 'jpg' ||
          fileExtension.toLowerCase() === 'gif' ||
          fileExtension.toLowerCase() === 'jpeg'
        ) {
          updatePhoto(files[0]);
          setProfilePicURL(URL.createObjectURL(files[0]));
        }
      }
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <div className="row">
                  <div className="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                    <h2>My Account</h2>
                  </div>
                  <div className="col-lg-4 col-md-4 col-sm-4 col-xs-12" />
                </div>
              </div>

              <div className="my-ac-section" id="my-account">
                <div className="my-ac-img">
                  {profilePic !== null ? (
                    <img
                      src={
                        profilePicURL !== '' ? profilePicURL : `${profilePic}`
                      }
                      alt="user"
                    />
                  ) : (
                    <img
                      src={require('../../../assets/img/user-image.png')}
                      alt="user"
                    />
                  )}

                  <div className="browse-btn">
                    <span>
                      <i className="fa fa-camera" aria-hidden="true" />
                    </span>
                    <input
                      type="file"
                      id="imgInp"
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => getImage(e)}
                    />
                  </div>
                </div>

                <Formik
                  initialValues={{
                    firstName: firstNameInitial,
                    lastName: lastNameInitial,
                    email,
                    phone: phoneInitial,
                  }}
                  validationSchema={validationSchema}
                  onSubmit={(values, { setSubmitting }) => {
                    updateProfileData(values);
                    setSubmitting(false);
                  }}
                >
                  {({
                    values,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    handleSubmit,
                  }) => (
                    <form onSubmit={handleSubmit}>
                      <div className="form-group usr-form">
                        <i className="fa fa-user" aria-hidden="true" />
                        <input
                          type="text"
                          name="firstName"
                          placeholder="First Name"
                          className="form-control padding-left-50"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.firstName}
                        />
                        <div className="error-message left-align">
                          {errors.firstName &&
                            touched.firstName &&
                            errors.firstName}
                        </div>
                      </div>
                      <div className="form-group usr-form">
                        <i className="fa fa-user" aria-hidden="true" />
                        <input
                          type="text"
                          name="lastName"
                          placeholder="Last Name"
                          className="form-control padding-left-50"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.lastName}
                        />
                        <div className="error-message left-align">
                          {errors.lastName &&
                            touched.lastName &&
                            errors.lastName}
                        </div>
                      </div>
                      <div className="form-group usr-form">
                        <i className="fa fa-envelope-o" aria-hidden="true" />
                        <input
                          type="text"
                          name="email"
                          placeholder="Email"
                          className="form-control padding-left-50"
                          disabled
                          value={values.email}
                          style={{ marginBottom: '10px' }}
                        />
                      </div>
                      <div className="form-group pass-word">
                        <i className="fa fa-phone" aria-hidden="true" />
                        <input
                          type="number"
                          name="phone"
                          className="form-control padding-left-50"
                          placeholder="Phone Number"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.phone}
                        />
                        <div className="error-message left-align">
                          {errors.phone && touched.phone && errors.phone}
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="change-pw-modal-a">
                          <button
                            type="button"
                            className="change-pw-modal btn-no-bg"
                            onClick={() => setModelOpen(true)}
                          >
                            <i className="fa fa-key" aria-hidden="true" />
                            &nbsp;Change Password
                          </button>
                        </p>
                      </div>
                      <div className="error-message">{errorMsg}</div>
                      <div className="clearfix">
                        <button
                          disabled={isDisable}
                          type="submit"
                          data-toggle="modal"
                          data-modal="dismiss"
                          data-target="#cong-modal"
                          className="btn btn-primary btn-block"
                        >
                          Update
                        </button>
                      </div>
                    </form>
                  )}
                </Formik>
              </div>
            </div>
          </div>
        </div>
      </div>
      {modelOpen && <ChangePasswordModal setModelOpen={setModelOpen} />}
      {open && <AlertMessageModal />}
    </div>
  );
};

export default SetTokenHeader(Profile, axios);
