import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Formik } from 'formik';
import * as Yup from 'yup';
import Model from '../../../components/UI/Model/Model';
import { changePassword, resetErrorMsg } from '../../../store/actions';
import {
  OLD_PASSWORD_REQUIRED,
  PASSWORD_VALIDATION,
  NEW_PASSWORD_REQUIRED,
  CHANGE_NEW_PASSWORDS_NOT_MATCH,
  CONFIRM_PASSWORD_REQUIRED,
} from '../../../constants/errorConstants';

// eslint-disable-next-line no-unused-vars
const ChangePasswordModal = props => {
  const [isDisable, setIsDisable] = useState(false);
  const dispatch = useDispatch();
  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
    dispatch(resetErrorMsg());
  };
  const { errorMsg } = useSelector(state => state.auth);

  const validationSchema = Yup.object({
    oldPassword: Yup.string().trim().required(OLD_PASSWORD_REQUIRED),
    newPassword: Yup.string()
      .matches(
        /^(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
        PASSWORD_VALIDATION,
      )
      .required(NEW_PASSWORD_REQUIRED),
    confirmPassword: Yup.string()
      .oneOf([Yup.ref('newPassword'), null], CHANGE_NEW_PASSWORDS_NOT_MATCH)
      .required(CONFIRM_PASSWORD_REQUIRED),
  });

  const submitBtnHandler = values => {
    setIsDisable(true);
    dispatch(
      changePassword({
        oldPassword: values.oldPassword,
        newPassword: values.newPassword,
        confirmPassword: values.confirmPassword,
        closeModel,
        setIsDisable,
      }),
    );
  };

  return (
    <Formik
      initialValues={{
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        submitBtnHandler(values);
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        // isSubmitting,
      }) => (
        <Model
          closeModel={closeModel}
          headerTitle="Change Password"
          submitBtnText="Submit"
          isDisable={isDisable}
          submitBtnHandler={handleSubmit}
        >
          <form>
            <span id="change-password-error" className="error text-danger" />
            <div className="form-group pw-form">
              <i className="fa fa-lock" aria-hidden="true" />
              <input
                type="password"
                className="form-control padding-left-50"
                placeholder="Old Password"
                id="old_password"
                name="oldPassword"
                value={values.oldPassword}
                onChange={handleChange}
                onBlur={handleBlur}
                // onChange={e => setOldPassword(e.target.value)}
              />
              <div className="error-message">
                {errors.oldPassword &&
                  touched.oldPassword &&
                  errors.oldPassword}
              </div>
            </div>
            <div className="form-group pw-form">
              <i className="fa fa-lock" aria-hidden="true" />
              <input
                type="password"
                className="form-control padding-left-50"
                placeholder="New Password"
                id="new_password"
                name="newPassword"
                value={values.newPassword}
                onChange={handleChange}
                onBlur={handleBlur}
                // onChange={e => setNewPassword(e.target.value)}
              />
              <div className="error-message">
                {errors.newPassword &&
                  touched.newPassword &&
                  errors.newPassword}
              </div>
            </div>
            <div className="form-group pw-form">
              <i className="fa fa-lock" aria-hidden="true" />
              <input
                type="password"
                className="form-control padding-left-50"
                placeholder="Confirm New Password"
                id="confirm_password"
                name="confirmPassword"
                value={values.confirmPassword}
                onChange={handleChange}
                onBlur={handleBlur}
                // onChange={e => setConfirmPassword(e.target.value)}
              />
              <div className="error-message">
                {errors.confirmPassword &&
                  touched.confirmPassword &&
                  errors.confirmPassword}
              </div>
            </div>
            <div className="error-message">{errorMsg}</div>
          </form>
        </Model>
      )}
    </Formik>
  );
};

export default ChangePasswordModal;
